<template>
	<view class="register">
		<u-navbar back-icon-color="#fff" title-color="#fff" title="我的动态" :background="background" title-bold>
			<view slot="right" style="margin-right: 30rpx;" @click="toSend()">
				<u-icon name="camera-fill" style="margin-right: 7rpx;"></u-icon>发布动态
			</view>
		</u-navbar>
		<view class="formBox">
			<view class="list02" v-for="(item,index) in list01" :key="index">
				<u-row>
					<u-col span="2">
						<u-avatar :src="item.header" size="90"></u-avatar>
					</u-col>
					<u-col span="10">
						<p class="titlebox">{{item.pushName}}</p>
						<p class="pbox">{{item.createTime}}</p>
					</u-col>
					<u-col span="12">
						<p class="pbox01 gray">{{item.activityContent}}</p>
					</u-col>
					<u-col span="6"
						style="text-align:center;border-bottom: 1px solid #ECECEC;padding: 10rpx 0 24rpx 0;">
						<image src="/static/img/head.png" mode="widthFix" style="width: 233rpx;"></image>
					</u-col>
					<u-col span="6"
						style="text-align:center;border-bottom: 1px solid #ECECEC;padding: 10rpx 0 24rpx 0;">
						<image src="/static/img/head.png" mode="widthFix" style="width: 233rpx;"></image>
					</u-col>
					<u-col span="10" style="margin-top: 20rpx;">
						<u-tag :text="item.universalAntiAboutMain26" mode="dark" />
					</u-col>
					<u-col span="2" style="margin-top: 20rpx;text-align: right;color: #999;">
						<span @click.stop="delDT(item.id)">删除</span>
					</u-col>
				</u-row>
			</view>
		</view>
			<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		antilistObj,
		delhdDt
	} from '../../api/index.js'
	export default {
		data() {
			return {
				params: {
					pageNum: 1,
					pageSize: 10
				},
				background: {
					backgroundColor: '#1F61BE',
					color: "#fff"
				},
				list01: [],
			}
		},
		onLoad() {
			this.params.queryJson=[{
				data1: 'pushUserId',
				data2: '0',
				data3: uni.getStorageSync('userInfo').userId,
				objectId: "302",
				subType: null,
				flag: false,
				fileType: "String"
			}];
			this.params.queryJson = JSON.stringify(this.params.queryJson);
			this.getlist();
		},
		methods: {
			toSend(){
				uni.navigateTo({
					url: 'release'
				})
			},
			getlist() {
				antilistObj(this.params).then(res => {
					if (res.code == 200) {
						this.list01 = res.rows;
					}
				})
			},
			delDT(id) {
				console.log("sid999",id);
				delhdDt(id).then(res => {
					if (res.code == 200) {
						this.$refs.uToast.show({
							title: '删除成功'
						});
						this.getlist();
					}
				})
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}

	.mybox {
		text-align: center;
		padding: 50rpx 0;
	}

	.formBox {}


	.list02 {
		width: 100%;
		height: 500rpx;
		background: #FFFFFF;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
		padding: 26rpx 30rpx 28rpx 30rpx;
		margin-bottom: 20rpx;
		box-shadow: 0px 2px 8px 1px rgba(0, 0, 0, 0.06);
	}

	.list02 .titlebox {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
	}

	.list02 .pbox {
		margin-top: 8rpx;
		font-size: 24rpx;
		color: #999999;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 .pbox01 {
		margin-top: 8rpx;
		font-size: 24rpx;
		color: #999999;
		width: 98%;
		overflow: hidden;
		/*文本超出隐藏*/
		display: -webkit-box;
		/*盒子模型微弹性伸缩模型*/
		-webkit-box-orient: vertical;
		/*伸缩盒子的子元素垂直排列*/
		-webkit-line-clamp: 2;
	}

	.list02 .gray {
		color: #333;
	}


	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}
</style>